.ranking-question {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.ranking-instructions {
  font-family: 'Courier New', monospace;
  font-size: 12px;
  color: var(--text-secondary);
  text-align: center;
  margin-top: -12px;
}

.ranking-items {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-top: 12px;
}

.ranking-item {
  font-family: 'Courier New', monospace;
  font-size: 14px;
  padding: 16px;
  background: transparent;
  border: 1px solid var(--text-secondary);
  color: var(--text-primary);
  cursor: grab;
  transition: all 0.2s ease;
  display: flex;
  gap: 12px;
  align-items: center;
  user-select: none;
  position: relative;
}

.ranking-item:active {
  cursor: grabbing;
}

.ranking-item:hover {
  border-color: var(--text-primary);
  transform: translateX(4px);
}

.ranking-item.dragging {
  opacity: 0.5;
  cursor: grabbing;
  border-color: var(--color-aim-purple);
  background: rgba(51, 153, 255, 0.1);
}

.ranking-number {
  font-weight: bold;
  color: var(--color-aim-yellow);
  min-width: 24px;
}

.ranking-text {
  flex: 1;
}

.ranking-drag-hint {
  color: var(--text-secondary);
  font-size: 16px;
  opacity: 0.5;
  line-height: 1;
}

.ranking-item:hover .ranking-drag-hint {
  opacity: 1;
  color: var(--color-aim-purple);
}

.ranking-actions {
  display: flex;
  gap: 12px;
  justify-content: center;
  margin-top: 12px;
}

.ranking-btn {
  font-family: 'Courier New', monospace;
  font-size: 14px;
  padding: 12px 24px;
  background: transparent;
  border: 1px solid var(--text-secondary);
  color: var(--text-primary);
  cursor: pointer;
  transition: all 0.2s ease;
}

.ranking-btn:hover:not(:disabled) {
  border-color: var(--text-primary);
  transform: translateY(-2px);
}

.ranking-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.ranking-btn-submit:hover:not(:disabled) {
  border-color: var(--color-aim-purple);
  color: var(--color-aim-purple);
}

.ranking-btn-skip {
  color: var(--text-secondary);
}

.ranking-btn-skip:hover:not(:disabled) {
  border-color: var(--color-aim-yellow);
  color: var(--color-aim-yellow);
}

/* Mobile styles */
@media (max-width: 768px) {
  .ranking-question {
    gap: var(--space-md);
  }

  .ranking-instructions {
    font-size: var(--text-xs);
  }

  .ranking-items {
    gap: var(--space-sm);
  }

  .ranking-item {
    padding: var(--space-md);
    font-size: var(--text-base);
    /* Better touch target for dragging */
    min-height: 60px;
    /* Improve touch dragging */
    touch-action: none;
  }

  .ranking-item:active {
    cursor: grabbing;
    transform: scale(1.02);
  }

  .ranking-number {
    font-size: var(--text-base);
  }

  .ranking-drag-hint {
    font-size: 20px;
  }

  .ranking-actions {
    flex-direction: column;
    gap: var(--space-sm);
  }

  .ranking-btn {
    width: 100%;
    min-height: 48px;
    font-size: var(--text-base);
    padding: var(--space-sm) var(--space-md);
  }
}
